import React, { Component } from "react";
import { connect } from "react-redux";
import * as userActions from "../../actions/userActions";
import store from "../../store/store";

export class LanguageToggler extends Component {
  handleLanguageChange = () => {
    this.props.lang === "Fr"
      ? store.dispatch(this.props.toggleLanguage("En"))
      : store.dispatch(this.props.toggleLanguage("Fr"));
  };
  render() {
    return (
      <li>
        <div className="switch teal" style={{ marginRight: "0.25rem" }}>
          <label className="white-text">
            <input onChange={this.handleLanguageChange} type="checkbox" />
            <span className="lever "></span>
            En
          </label>
        </div>
      </li>
    );
  }
}

let mapStateToProps = ({ user: { lang } }) => ({ lang });
export default connect(mapStateToProps, userActions)(LanguageToggler);
