import React from "react";
import { BrowserRouter, Route } from "react-router-dom";
import Header from "./Helpers/Header";
import LandingPage from "./Pages/LandingPage";
import { connect } from "react-redux";
import * as userActions from "../actions/userActions";
import NewUser from "./Infos/NewUser";
import DonePage from "./Pages/DonePage";
import { Helmet } from "react-helmet";
import Footer from "./Helpers/Footer";
import ProfilePage from "./Pages/ProfilePage";
import OwnSkillsPage from "./Pages/OwnSkillsPage";
class App extends React.Component {
  componentDidMount() {
    this.props.fetchUser();
  }
  render() {
    return (
      <BrowserRouter>
        <Helmet>
          <meta charSet="utf-8" />
          <title>Sa9ssini</title>
          <link rel="canonical" href="https://sa9ssini.herokuapp.com/" />
        </Helmet>
        <Header />
        <main>
          <Route exact path="/" component={LandingPage} />
          <Route exact path="/profile" component={ProfilePage} />
          <Route exact path="/updateProfile" component={NewUser} />
          <Route exact path="/ownSkills" component={OwnSkillsPage} />
          <Route exact path="/done" component={DonePage} />
        </main>
        <Footer lang={this.props.lang} />
      </BrowserRouter>
    );
  }
}

let mapStateToProps = ({ user: { lang } }) => ({ lang });
export default connect(mapStateToProps, { ...userActions })(App);
