import {
  FETCH_USER,
  UPDATE_USER,
  FETCH_RESULTS,
  TOGGLE_LANGUAE,
} from "../actions/types";

export default (state = { lang: "Fr" }, action) => {
  switch (action.type) {
    case FETCH_USER:
      return { ...state, ...action.payload };
    case UPDATE_USER:
      return { ...state, ...action.payload };
    case FETCH_RESULTS: {
      return { ...state, results: action.payload };
    }
    case TOGGLE_LANGUAE: {
      return { ...state, lang: action.payload };
    }
    default:
      return state;
  }
};
