import axios from "axios";

import {
  FETCH_USER,
  UPDATE_USER,
  FETCH_RESULTS,
  TOGGLE_LANGUAE,
} from "./types";

export const fetchUser = () => async (dispatch) => {
  try {
    let { data } = await axios.get("/api/users/me");
    dispatch({
      type: FETCH_USER,
      payload: data,
    });
  } catch (error) {
    console.log(error);
  }
};

export const updateUser = (updates = {}) => async (dispatch) => {
  try {
    let { data } = await axios.patch("/api/users/me", updates);
    dispatch({
      type: UPDATE_USER,
      payload: data,
    });
  } catch (error) {
    console.log(error);
  }
};

export const fetchResults = () => async (dispatch) => {
  try {
    let { data } = await axios.get("/api/users");
    dispatch({
      type: FETCH_RESULTS,
      payload: data,
    });
  } catch (error) {
    console.log(error);
  }
};

export const toggleLanguage = (language) => {
  return {
    type: TOGGLE_LANGUAE,
    payload: language,
  };
};
