//import axios from "axios";
import {
  UPDATE_GENDER,
  UPDATE_PHONENBR,
  UPDATE_UNIVERSITY,
  UPDATE_AVAILBLE_AT,
  UPDATE_CITY,
  UPDATE_ABOUT,
} from "./types";

export const updateGender = (gender) => {
  return {
    type: UPDATE_GENDER,
    payload: gender,
  };
};

export const updatePhoneNbr = (phoneNbr) => {
  return {
    type: UPDATE_PHONENBR,
    payload: phoneNbr,
  };
};

export const updateAvailbleAt = (availbleAt) => {
  return {
    type: UPDATE_AVAILBLE_AT,
    payload: availbleAt,
  };
};

export const updateUniversity = (university) => {
  return {
    type: UPDATE_UNIVERSITY,
    payload: university,
  };
};

export const updateCity = (city) => {
  return {
    type: UPDATE_CITY,
    payload: city,
  };
};

export const updateAbout = (text) => {
  return {
    type: UPDATE_ABOUT,
    payload: text,
  };
};
