import moment from "moment";
export let badge = (name, value) => {
  switch (name) {
    case "startDate": {
      value = moment.utc(value).toDate();
      return value.toString().split(" 00:00:00 ")[0];
    }
    case "endDate": {
      value = moment.utc(value).toDate();
      return value.toString().split(" 00:00:00 ")[0];
    }
    case "budget": {
      if (value.min === 100) {
        return `max: ${value.max} euros`;
      }
      return `min: ${value.min} eur -- max: ${value.max}`;
    }
    case "guarantor": {
      return value === "Yes" ? "Yes" : "No";
    }
    case "tenant": {
      return value === "Yes" ? "Yes" : "No";
    }
    default:
      return value;
  }
};
