import React, { Component } from "react";
import NewUserForm from "./NewUserForm";
import "materialize-css/dist/css/materialize.css";
import ReviewInfos from "./ReviewInfos";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import * as userActions from "../../actions/userActions";

export class NewUser extends Component {
  state = {
    review: false,
  };
  componentDidMount() {
    this.props.fetchUser();
  }

  onSurveySubmit = () => {
    this.setState({ review: true });
  };

  onCancel = () => {
    this.setState({ review: false });
  };

  showForm = () => {
    if (this.state.review) {
      return <ReviewInfos onCancel={this.onCancel} />;
    }
    return <NewUserForm onSubmit={this.onSurveySubmit} />;
  };

  render() {
    return <div className="container">{this.showForm()}</div>;
  }
}

let mapStateToProps = ({ user: { user } }) => ({ user });
export default connect(mapStateToProps, userActions)(NewUser);
