import React, { Component } from "react";
import FormInit from "../../../helpers/FormInit";
import * as dataActions from "../../../actions/dataActions";
import store from "../../../store/store";
export class InputField extends Component {
  componentDidMount() {
    FormInit(this.props.identifier);
  }
  onChange = (id) => (event) => {
    event.preventDefault();
    let value = event.target.value;
    switch (id) {
      case "phoneNbr": {
        return store.dispatch(dataActions.updatePhoneNbr(value));
      }
      case "universityAutocomplete": {
        return store.dispatch(dataActions.updateUniversity(value));
      }
      case "cityAutocomplete": {
        return store.dispatch(dataActions.updateCity(value));
      }
      default:
        return;
    }
  };
  render() {
    return (
      <div className="row">
        <div className="input-field col s12">
          <i className="material-icons prefix">{this.props.icon}</i>
          <input
            id={this.props.identifier}
            type={this.props.type ? this.props.type : "text"}
            onChange={this.onChange(this.props.identifier)}
            autoComplete="off"
            name={this.props.name}
            className={`${this.props.identifier}`}
            //  required
          />
          <label htmlFor={this.props.identifier}>{this.props.label}</label>
        </div>
      </div>
    );
  }
}
