import React, { Component } from "react";
import { connect } from "react-redux";
import "materialize-css/extras/noUiSlider/nouislider.css";
import { Link } from "react-router-dom";
import { Inputs, Selects, TextAreas } from "../Helpers/Fields";
import { InputField, SelectField, TextAreaField } from "./FormFields";
import ChipesAuto from "./HelperComponents/ChipsAuto";

const Fields = {
  Fr: {
    title: "Veuillez completer votre profile",
    nextBtn: "Suivant",
    prevBtn: "Annuler",
  },
  En: {
    title: "Please complete your profile",
    nextBtn: "Next",
    prevBtn: "Cancel",
  },
};

export class NewUserForm extends Component {
  renderInputFields = () => {
    return Inputs[this.props.lang].map((field) => (
      <InputField key={field.name} {...field} />
    ));
  };
  renderSelectFields = () => {
    return Selects[this.props.lang].map((field) => (
      <SelectField key={field.name} {...field} />
    ));
  };
  renderTextAreaFields = () => {
    return TextAreas[this.props.lang].map((field) => (
      <TextAreaField key={field.name} {...field} />
    ));
  };

  render() {
    return (
      <div style={{ padding: "0.625rem", margin: "1.25rem" }}>
        <h1 style={{ fontSize: "2.5rem" }} className="center">
          {Fields[this.props.lang].title}
        </h1>
        <form className="center-align" onSubmit={this.props.onSubmit}>
          {this.renderInputFields()}
          {this.renderTextAreaFields()}
          {this.renderSelectFields()}
          <ChipesAuto />

          <Link
            style={{ fontSize: "1rem" }}
            to="/"
            className="red left btn-flat white-text"
          >
            {Fields[this.props.lang].prevBtn}
          </Link>
          <button
            onSubmit={this.onFormSubmit}
            className="teal btn-flat right white-text"
            type="submit"
            style={{ fontSize: "1rem" }}
          >
            {Fields[this.props.lang].nextBtn}
            <i className="material-icons right">done</i>
          </button>
        </form>
      </div>
    );
  }
}

let mapStateToProps = ({ user: { lang } }) => ({ lang });
export default connect(mapStateToProps)(NewUserForm);
