import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import LanguageToggler from "./LanguageToggeler";
import Chip from "../Helpers/Chip";
class Header extends Component {
  renderNavContent() {
    if (this.props.auth.userName) {
      return [
        <li key="logout">
          <a href="/api/auth/logout">Logout</a>
        </li>,
        <li className="hide-on-large-only" key="AddInfosSmall">
          <Link to="/Profile">Profile</Link>
        </li>,
        <LanguageToggler key="toggleLanguage" />,
        <li key="AddInfos">
          <Link to="/Profile">
            <Chip />
          </Link>
        </li>,
      ];
    } else {
      return [
        <li key="login">Login :</li>,
        <li key="GithubSignIn">
          <a href="/auth/github">
            <i
              style={{ marginRight: "2px" }}
              className="fa fa-google"
              aria-hidden="true"
            ></i>{" "}
            Github{" "}
          </a>
        </li>,
        <li key="LinkedinSignIn">
          <a href="/auth/linkedin">
            <i
              style={{ marginRight: "3px" }}
              className="fa fa-linkedin"
              aria-hidden="true"
            ></i>
            Linkedin
          </a>
        </li>,
        <LanguageToggler key="toggleLanguage" />,
      ];
    }
  }
  render() {
    return (
      <header>
        <nav style={{ height: "4,3rem" }}>
          <div className="nav-wrapper teal">
            <div style={{ marginLeft: "1.25rem" }}>
              <Link
                style={{ fontSize: "1.5rem" }}
                to="/"
                className="left brand-logo"
              >
                Sa9ssini
              </Link>
            </div>

            <ul className="right">{this.renderNavContent()}</ul>
          </div>
        </nav>
      </header>
    );
  }
}

const mapStateToProps = ({ auth }) => {
  return { auth };
};

export default connect(mapStateToProps)(Header);
