import {
  UPDATE_GENDER,
  UPDATE_PHONENBR,
  UPDATE_UNIVERSITY,
  UPDATE_AVAILBLE_AT,
  UPDATE_CITY,
  UPDATE_ABOUT,
  UPDATE_SKILLS,
} from "../actions/types";
export default (state = {}, actions) => {
  switch (actions.type) {
    case UPDATE_GENDER: {
      return { ...state, gender: actions.payload };
    }
    case UPDATE_PHONENBR: {
      return { ...state, phoneNbr: actions.payload };
    }

    case UPDATE_UNIVERSITY: {
      return { ...state, university: actions.payload };
    }
    case UPDATE_CITY: {
      return { ...state, city: actions.payload };
    }
    case UPDATE_AVAILBLE_AT: {
      return { ...state, availbleAt: actions.payload };
    }
    case UPDATE_ABOUT: {
      return { ...state, about: actions.payload };
    }
    default:
      return state;
  }
};
