import React, { Component } from "react";
import Slider from "../Helpers/Slider";
import { connect } from "react-redux";
const Fields = {
  Fr: { description: "Description" },
  En: { description: "Description" },
};
export class Landing extends Component {
  Infolang() {
    return this.props.lang === "Fr" ? Fields["Fr"] : Fields["En"];
  }
  render() {
    return (
      <div>
        <h1 style={{ fontSize: "7.5rem" }} className="center teal-text">
          Sa9ssini
        </h1>
        <h3 className="center">{this.Infolang().description}</h3>
        <Slider options={{ height: 300, width: 300 }} />
      </div>
    );
  }
}
let mapStateToProps = ({ user: { lang } }) => ({ lang });
export default connect(mapStateToProps)(Landing);
