import React, { Component } from "react";
import { connect } from "react-redux";
import { Inputs, Selects } from "../Helpers/Fields";
import * as userActions from "../../actions/userActions";
import * as skillsActions from "../../actions/skillsActions";
import { withRouter } from "react-router";
import { badge } from "../../helpers/utils";
let Fields = {
  Fr: {
    title: "Veuillez confirmer vos entrées",
    nextBtn: "Soumettre",
    prevBtn: "Annuler",
  },
  En: {
    title: "Please confirm your entries",
    nextBtn: "Submit",
    prevBtn: "Cancel",
  },
};
export class ReviewInfos extends Component {
  onSubmit = ({ updateUser, form, history, formSkills }) => () => {
    formSkills.forEach((skill) => {
      this.props.addSkill({ skill });
    });
    updateUser(form);
    this.props.fetchResults();
    history.push("/profile");
  };
  renderBasicComponents = (form) => {
    return Inputs[this.props.lang]
      .concat(Selects[this.props.lang])
      .map(({ name, label, icon }) => (
        <li key={name}>
          <div className="collapsible-header">
            <i className="material-icons">{icon}</i>
            {label}
            <span
              className="badge"
              data-badge-caption={badge(name, form[name])}
            ></span>
          </div>
        </li>
      ));
  };

  render() {
    return (
      <div className="container">
        <h3 className="center">{Fields[this.props.lang].title}</h3>
        <div>
          <ul style={{ margin: "1.25rem" }} className="collapsible">
            {this.renderBasicComponents(this.props.form)}
          </ul>
        </div>
        <button
          onClick={this.props.onCancel}
          className="btn waves-effect  waves-light orange darken-1 left"
        >
          {Fields[this.props.lang].prevBtn}{" "}
          <i className="material-icons right">cancel_schedule_send</i>
        </button>
        <button
          onClick={this.onSubmit(this.props)}
          className="btn waves-effect waves-light right"
        >
          {Fields[this.props.lang].nextBtn}
          <i className="material-icons right">send</i>
        </button>
      </div>
    );
  }
}

let mapStateToProps = ({ form, user: { lang }, skills: { formSkills } }) => ({
  form,
  lang,
  formSkills,
});
export default connect(mapStateToProps, { ...skillsActions, ...userActions })(
  withRouter(ReviewInfos)
);
