import React, { Component } from "react";
import { connect } from "react-redux";
import * as skillsActions from "../../actions/skillsActions";
import FormInit from "../../helpers/FormInit";

let Fields = {
  Fr: {
    title: "Salut temp for ownSkills",
    subtitle: "subtitle here",
  },
  En: {
    title: "Hi temp",
    subtitle: "sub tit here en",
  },
};

let SkillDetailsFieldsHelper = {
  Fr: [
    { icon: "wb_incandescent", label: "Compétance", name: "skill" },
    { icon: "person_pin", label: "Utilisateur", name: "owner" },
    { icon: "poll", label: "Votes", name: "raters" },
    { icon: "description", label: "Description", name: "description" },
  ],
  En: [
    { icon: "wb_incandescent", label: "Skill", name: "skill" },
    { icon: "person_pin", label: "User", name: "owner" },
    { icon: "poll", label: "Rates", name: "raters" },
    { icon: "description", label: "Description", name: "description" },
  ],
};

export class OwnSkillsPage extends Component {
  componentDidMount() {
    FormInit("collapsible");
    this.props.getMySkills();
  }

  renderSkillDetails = (skill) => {
    return SkillDetailsFieldsHelper[this.props.lang].map(
      ({ icon, label, name }) => (
        <li key={icon}>
          <div className="collapsible-header">
            <i className="material-icons">{icon}</i>
            {label}
            <span
              style={{ marginTop: ".5rem" }}
              className="badge"
              data-badge-caption={""}
            >
              {name === "raters"
                ? skill[name].length
                : name === "owner"
                ? skill[name].userName
                : skill[name]}
            </span>
          </div>
        </li>
      )
    );
  };
  renderSkills = () => {
    if (this.props.skills) {
      console.log(this.props.skills);
      return this.props.skills.map((skill) => {
        return (
          <li key={skill._id}>
            <div className="collapsible-header">
              <h6 className="center">{skill.skill}</h6>{" "}
              <span
                style={{ marginTop: ".5rem" }}
                className="badge"
                data-badge-caption={"Press To Open"}
              ></span>
            </div>
            <div className="collapsible-body">
              <ul className="collapsible">{this.renderSkillDetails(skill)}</ul>
              <div className="center">
                <h5>
                  {this.props.lang === "Fr" ? "Description" : "Description"}
                </h5>
              </div>
            </div>
          </li>
        );
      });
    }
  };
  render() {
    return (
      <div className="container center">
        <h1
          style={{ fontSize: "3rem" }}
          className="center teal-text text-darken-2"
        >
          {Fields[this.props.lang].title}
          {this.props.userName} !
        </h1>
        <h3 style={{ fontSize: "2rem" }} className="center">
          {Fields[this.props.lang].subtitle}
        </h3>
        <div className="container">
          <ul className="collapsible">{this.renderSkills()}</ul>
        </div>
      </div>
    );
  }
}

let mapStateToProps = ({ skills: { mySkills }, user: { lang } }) => ({
  skills: mySkills,
  lang,
});

export default connect(mapStateToProps, skillsActions)(OwnSkillsPage);
