import mcss from "materialize-css";

import { cities, universities } from "./searchOptions";

import * as dataActions from "../actions/dataActions";
import * as skillsActions from "../actions/skillsActions";
import store from "../store/store";
export default (identifier, { height, width, hidden, lang } = {}) => {
  switch (identifier) {
    case "universityAutocomplete": {
      let university = document.querySelector(".universityAutocomplete");
      mcss.Autocomplete.init(university, {
        data: universities,
        onAutocomplete: (val) => {
          store.dispatch(dataActions.updateUniversity(val));
        },
      });
      store.dispatch(dataActions.updateUniversity(""));
      break;
    }
    case "cityAutocomplete": {
      let city = document.querySelector(".cityAutocomplete");
      mcss.Autocomplete.init(city, {
        data: cities,

        onAutocomplete: (val) => {
          store.dispatch(dataActions.updateCity(val));
        },
      });
      store.dispatch(dataActions.updateCity(""));
      break;
    }

    case "GenderSelect": {
      let gender = document.querySelector(".GenderSelect");
      mcss.FormSelect.init(gender);
      store.dispatch(dataActions.updateGender("Male"));

      break;
    }

    case "timepicker": {
      let times = document.querySelector(".timepicker");
      mcss.Timepicker.init(times, {
        autoClose: true,
        twelveHour: false,
        onSelect: (hour, minut) => {
          if (!minut) {
            minut = "00";
          }
          store.dispatch(dataActions.updateAvailbleAt(`${hour}h:${minut} min`));
        },
      });
      store.dispatch(dataActions.updateAvailbleAt(`18h:00min`));

      break;
    }

    case "collapsible": {
      let collapsible = document.querySelectorAll(".collapsible");
      mcss.Collapsible.init(collapsible);

      break;
    }
    case "textarea": {
      let textarea = document.querySelector(".textarea");
      mcss.CharacterCounter.init(textarea);
      store.dispatch(dataActions.updateAbout("rien à signaler"));
      break;
    }
    case "slider": {
      let slider = document.querySelectorAll(".slider");
      mcss.Slider.init(slider, {
        indicators: true,
        height,
        width,
        duration: 500,
        interval: 8000,
      });

      break;
    }
    case "chipsAuto": {
      let skills = [];

      var elems = document.querySelectorAll(".chips");
      mcss.Chips.init(elems, {
        placeholder: "Compétances",
        limit: 3,
        onChipAdd: () => {
          document.querySelectorAll(".chips>div").forEach((chip) => {
            if (!skills.includes(chip.firstChild.data)) {
              skills.push(chip.firstChild.data);
            }
          });
          store.dispatch(skillsActions.updateSkills(skills));
        },
        onChipDelete: (el, chip) => {
          let index = skills.indexOf(chip.firstChild.data);
          if (index > -1) {
            skills.splice(index, 1);
          }
          store.dispatch(skillsActions.updateSkills(skills));
        },
        autocompleteOptions: {
          data: {
            Javascript: null,
            ReactJs: null,
            NodeJs: null,
          },
        },
      });
      break;
    }

    default:
      return;
  }
};
