import axios from "axios";

import {
  ADD_SKILL,
  DELETE_SKILL,
  GET_MY_SKILLS,
  GET_OTHERS_SKILLS,
  GET_SKILL_BY_NAME,
  RATE_SKILL,
  UPDATE_SKILL,
  UPDATE_SKILLS,
} from "./types";

export const addSkill = (skill) => async (dispatch) => {
  try {
    let { data } = await axios.post("/api/skills", skill);
    dispatch({
      type: ADD_SKILL,
      payload: data,
    });
  } catch (error) {
    console.log(error);
  }
};

export const deleteSkill = (skillId) => async (dispatch) => {
  try {
    let { data } = await axios.delete(`/api/skills/${skillId}`);
    console.log(data);
    dispatch({
      type: DELETE_SKILL,
      payload: data,
    });
  } catch (error) {
    console.log(error);
  }
};

export const getMySkills = () => async (dispatch) => {
  try {
    let { data } = await axios.get(`/api/skills/me`);
    dispatch({
      type: GET_MY_SKILLS,
      payload: data,
    });
  } catch (error) {
    console.log(error);
  }
};

export const getOthersSkills = (userId) => async (dispatch) => {
  try {
    let { data } = await axios.get(`/api/skills/user/${userId}`);
    console.log(data);
    dispatch({
      type: GET_OTHERS_SKILLS,
      payload: data,
    });
  } catch (error) {
    console.log(error);
  }
};

export const getSkillByName = (skillName) => async (dispatch) => {
  try {
    let { data } = await axios.get(`/api/skills/${skillName}`);
    console.log(data);
    dispatch({
      type: GET_SKILL_BY_NAME,
      payload: data,
    });
  } catch (error) {
    console.log(error);
  }
};

export const rateSkill = (skillId, rate) => async (dispatch) => {
  try {
    let { data } = await axios.get(`/api/skills/${skillId}/rate`, rate);
    console.log(data);
    dispatch({
      type: RATE_SKILL,
      payload: data,
    });
  } catch (error) {
    console.log(error);
  }
};

export const updateSkill = (skillId, description = "") => async (dispatch) => {
  try {
    let { data } = await axios.patch(`/api/skills/me/${skillId}`, {
      description,
    });
    console.log(data);
    dispatch({
      type: UPDATE_SKILL,
      payload: data,
    });
  } catch (error) {
    console.log(error);
  }
};

export const updateSkills = (skills) => {
  console.log(skills);
  return {
    type: UPDATE_SKILLS,
    payload: skills,
  };
};
