import React, { Component } from "react";
import FormInit from "../../../helpers/FormInit";
import * as dataActions from "../../../actions/dataActions";
import store from "../../../store/store";
export class TextAreaField extends Component {
  componentDidMount() {
    FormInit("textarea");
  }

  onChange = (id) => ({ target: { value } }) => {
    switch (id) {
      case "about-textArea": {
        store.dispatch(dataActions.updateAbout(value));
        break;
      }
      default:
        return;
    }
  };
  render() {
    return (
      <div className="row">
        <div className="input-field">
          <i className="material-icons prefix">{this.props.icon}</i>
          <textarea
            onChange={this.onChange(this.props.identifier)}
            id="textarea"
            className="materialize-textarea textarea"
            data-length="255"
            maxLength="256"
            required
          ></textarea>
          <label htmlFor="textarea">{this.props.label}</label>
        </div>
      </div>
    );
  }
}
