import React, { Component } from "react";
import { connect } from "react-redux";

class ProfileChip extends Component {
  render() {
    return (
      <div
        style={{ marginTop: "1.2rem" }}
        className="chip right hide-on-med-and-down"
      >
        <img
          src={this.props.user ? this.props.user.imgUrl : ""}
          alt="ProfileChip"
        />
        {this.props.userName}
      </div>
    );
  }
}

let mapStateToProps = ({ auth: { userName, user } }) => ({ userName, user });

export default connect(mapStateToProps)(ProfileChip);
