import React, { Component } from "react";
import FormInit from "../../../helpers/FormInit";
import * as dataActions from "../../../actions/dataActions";
import store from "../../../store/store";
export class SelectField extends Component {
  componentDidMount() {
    FormInit(this.props.identifier);
  }
  renderOptions = (options) => {
    return options.map((option) => (
      <option key={option} value={option}>
        {option}
      </option>
    ));
  };

  onSelect = (id) => (event) => {
    switch (id) {
      case "GenderSelect": {
        return store.dispatch(dataActions.updateGender(event.target.value));
      }

      default:
        return;
    }
  };
  render() {
    return (
      <div className="row input-field">
        <div className="col s12 m4 l1">
          <i style={{ display: "block" }} className="material-icons">
            {this.props.icon}
          </i>
          <label>{this.props.label}</label>
        </div>
        <div className="col s12 m4 l11">
          <select
            onChange={this.onSelect(this.props.identifier)}
            className={this.props.identifier}
            required
          >
            {this.renderOptions(this.props.options)}
          </select>
        </div>
      </div>
    );
  }
}
