import React, { Component } from "react";
import { connect } from "react-redux";
import FormInit from "../../helpers/FormInit";
import Spinner from "../Helpers/Spinner";
import { Link, Redirect } from "react-router-dom";
import * as userActions from "../../actions/userActions";
let Fields = {
  Fr: {
    title: "Salut temp for profile",
    subtitle: "subtitle here",
    checkSKills: "Vérifier vos compétances",
    updateProfile: "mettre à jour le profil",
  },
  En: {
    title: "Hi temp",
    subtitle: "sub tit here en",
    checkSKills: "Check your skills",
    updateProfile: "update Profile",
  },
};

let ProfileFields = [
  {
    icon: "person",
    label: { Fr: "nom d'utilisateur", En: "Username" },
    name: "userName",
  },
  {
    icon: "school",
    label: { Fr: "Université", En: "University" },
    name: "university",
  },
  { icon: "location_city", label: { Fr: "Ville", En: "City" }, name: "city" },
  {
    icon: "phone",
    label: { Fr: "Téléphone", En: "Phone number" },
    name: "phoneNbr",
  },
  { icon: "face", label: { Fr: "Sexe", En: "Gender" }, name: "gender" },
];
export class profilePage extends Component {
  componentDidMount() {
    this.props.fetchUser();
    FormInit("collapsible");
  }
  renderProfileDetails = (profile) => {
    return (
      <div>
        {ProfileFields.map(({ icon, label, name }) => (
          <li key={name}>
            <div className="collapsible-header">
              <i className="material-icons">{icon}</i>
              {label[this.props.lang]}
              <span className="badge" data-badge-caption={profile[name]}></span>
            </div>
          </li>
        ))}
      </div>
    );
  };

  renderProfile = (profile) => {
    if (profile) {
      return (
        <li className="active" key="profile">
          <div className="collapsible-header">
            <img
              style={{ width: "3rem", marginRight: "1rem" }}
              className="circle"
              src={this.props.profile.imgUrl}
            />
            <h6 className="center">{this.props.profile.userName}</h6>{" "}
            <span
              style={{ marginTop: ".5rem" }}
              className="badge"
              data-badge-caption={"Press To Open"}
            ></span>
          </div>
          <div className="collapsible-body">
            <ul className="collapsible">
              {this.renderProfileDetails(profile)}
            </ul>
            <div className="center">
              <h5>{this.props.lang === "Fr" ? "Bio" : "About"}</h5>
              <p>{profile.about}</p>
            </div>
          </div>
        </li>
      );
    } else {
      return <Spinner />;
    }
  };

  showContent = () => {
    return (
      <div className="center container">
        <h1
          style={{ fontSize: "3rem" }}
          className="center teal-text text-darken-2"
        >
          {Fields[this.props.lang].title}
          {this.props.userName} !
        </h1>
        <h3 style={{ fontSize: "2rem" }} className="center">
          {Fields[this.props.lang].subtitle}
        </h3>

        <div className="container">
          <ul className="collapsible">
            {this.renderProfile(this.props.profile)}
          </ul>
          <Link className="teal btn-flat white-text left" to="updateProfile">
            {Fields[this.props.lang].updateProfile}
            <i className="material-icons right">done</i>
          </Link>
          <Link className="teal btn-flat white-text right" to="OwnSKills">
            {Fields[this.props.lang].checkSKills}{" "}
            <i className="material-icons right">done_outline</i>
          </Link>
        </div>
      </div>
    );
  };

  render() {
    return <div> {this.showContent()}</div>;
  }
}

let mapStateToProps = ({ user: { lang, user } }) => ({
  profile: user,
  lang,
});
export default connect(mapStateToProps, userActions)(profilePage);
