import {
  ADD_SKILL,
  DELETE_SKILL,
  GET_MY_SKILLS,
  GET_OTHERS_SKILLS,
  GET_SKILL_BY_NAME,
  RATE_SKILL,
  UPDATE_SKILL,
  UPDATE_SKILLS,
} from "../actions/types";

export default (state = {}, action) => {
  switch (action.type) {
    case UPDATE_SKILL:
      return { ...state, ...action.payload };
    case DELETE_SKILL:
      return { ...state, ...action.payload };
    case GET_MY_SKILLS: {
      return { ...state, mySkills: action.payload };
    }
    case GET_OTHERS_SKILLS: {
      return { ...state, othersSkills: action.payload };
    }
    case GET_SKILL_BY_NAME: {
      return { ...state, skillByName: action.payload };
    }
    case RATE_SKILL: {
      return { ...state, rate: action.payload };
    }
    case UPDATE_SKILLS: {
      return { ...state, formSkills: action.payload };
    }

    default:
      return state;
  }
};
